.footer-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-gap: clamp(30px, 6.364vw, 70px);
  padding: 60px 0 40px;


  @include tablet{
    grid-gap: 10px;
  }
  
  &__inner {
    display: flex;
    align-items: center;

    @include small-tablet{
      display: none;
    }
  }

  &__content {
    display: flex;
    align-items: center;
    grid-gap: 20px;
  }
}
